import { useState } from "react";
import "./styles.css";

let escolheNumero = Math.floor(Math.random() * 301);

export default function App() {
  const [estado, setEstado] = useState("Entrada");
  const [palpite, setPalpite] = useState(Math.floor(Math.random() * 301));
  const [numPalpites, setNumPalpites] = useState(1);
  const [min, setMin] = useState(0);
  const [max, setMax] = useState(300);

  const iniciarJogo = () => {
    escolheNumero = Math.floor(Math.random() * 301);
    setEstado("Rodando");
    setMin(0);
    setMax(300);
    setNumPalpites(1);
  };

  const menor = () => {
    setNumPalpites(numPalpites + 1);
    setMax(palpite);
    setMin(min);

    const proximoPalpite = Math.floor(
      Math.random() * (palpite + 1 - min) + min
    );
    setPalpite(proximoPalpite);
  };

  const maior = () => {
    setNumPalpites(numPalpites + 1);
    setMax(max);
    setMin(palpite);

    const proximoPalpite = Math.floor(
      Math.random() * (max + 1 - palpite) + palpite
    );
    setPalpite(proximoPalpite);
  };

  const acertou = () => {
    setEstado("Fim");
  };

  if (estado === "Entrada") {
    return <button onClick={iniciarJogo}>Jogar</button>;
  }

  if (estado === "Fim") {
    return (
      <div>
        <h1>Acertou Mizeravi</h1>
        <p>
          Precisou de {numPalpites} tentativas para acertar o número{" "}
          {escolheNumero}.
        </p>
        <button onClick={iniciarJogo}>Jogar</button>
      </div>
    );
  }

  return (
    <div className="App">
      <p>Número escolhido pela máquina {escolheNumero}</p>
      <p>O número é {palpite}?</p>
      <p>
        O número está entre {min} e {max}
      </p>
      <button onClick={menor}>É menor</button>
      <button onClick={acertou}>Acertou Mizeravi</button>
      <button onClick={maior}>É maior</button>
    </div>
  );
}
